.menu {
  @include flex(flex-start, normal, row, wrap);
  margin-left: -15px;
  margin-right: -15px;
  @include md {
    margin-left: -10px;
    margin-right: -10px; }
  &__toggle {
    display: none;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 25px;
    @include lg {
      display: block; }
    @include xs {
      width: 100%; } }

  &__aside {
    width: percentage(3/12);
    padding-left: 15px;
    padding-right: 15px;
    @include lg {
      position: fixed;
      z-index: 9;
      top: 0;
      bottom: 0;
      width: 320px;
      background-color: $black;
      overflow: auto;
      padding-top: 30px;
      padding-bottom: 30px;
      @include tr(transform .3s);
      transform: translate(-102%, 0);
      &.is-active {
        transform: translate(0, 0); } }
    @include md {
      padding-left: 10px;
      padding-right: 10px; }
    @include xss {
      width: 270px; } }
  &__items {
    width: percentage(9/12);
    // +flex(flex-start, normal, row, wrap)
    // +clr
    @include lg {
      width: 100%; } }
  &__items-in {
    @include flex(flex-start, normal, row, wrap); }
  &__title {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__item {
    // float: left
    padding-left: 15px;
    padding-right: 15px;
    width: 33.33333%;
    margin-bottom: 15px;
    @include md {
      padding-left: 10px;
      padding-right: 10px; }
    @include sm {
      width: 50%; }
    @include xs {
      width: 100%; } } }

.menu-nav {
  color: $white;
  &__item {
    @include flex(flex-start, center);
    // +notlast
    //   margin-bottom: 30px
    //   +md
    //     margin-bottom: 15px
    img {
      display: inline-block;
      margin-right: 10px;
      @include xss {
        width: 50px; } } }
  &__link {
    display: inline-block;
    border: 1px solid transparent;
    padding: 7px;
    border-radius: 20px;
    @include tr(color .3s, border-color .3s, background-color .3s);
    @include xss {
      padding: 4px; }
    &.is-active {
      border-color: $accent;
      color: $white;
      background-color: $accent; }
    @include hover {
      @extend .menu-nav__link.is-active; } } }
