.item {
  $this: &;
  position: relative;
  // height: 100%
  text-align: center;
  padding-top: 40px;
  border: 1px solid $c-border;
  border-radius: 15px;
  background-color: $gray-dark;
  @include tr(border-color .3s);
  margin-bottom: 45px;
  height: calc(100% - 45px);
  @include flex(flex-end, normal, column);
  @include hover {
    border-color: $accent; }
  @include sm {
    margin-bottom: 40px;
    height: calc(100% - 40px); }
  @include xs {
    margin-bottom: 30px;
    height: calc(100% - 30px); }

  &__img {
    // +cover
    // background-size: contain
    // padding-top: 40%
    margin-bottom: 20px;
    flex-grow: 1; }
  &__title {
    color: $white-dark;
    @include semi;
    text-transform: uppercase;
    margin-bottom: 35px;
    @include lg {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 15px; } }
  &__price {
    margin-bottom: 10px;
    @include flex(center, flex-end);
    .price {
      margin-right: 10px; } }
  &__label {
    position: absolute;
    top: 7px;
    left: 7px;
    z-index: 1;
    background-color: $white;
    @include bold;
    text-transform: uppercase;
    font-size: 13px;
    color: $accent;
    border-radius: 15px;
    padding: 6px 10px;
    min-width: 112px;
    text-align: center; }
  &__label-age {
    color: $white;
    background-color: $accent;
    padding: 0;
    height: 25px;
    min-width: 25px;
    line-height: 25px;
    text-align: center;
    right: 15px;
    top: 15px;
    left: auto; }
  &__counter {
    margin-bottom: 20px; }
  &__btn {
    margin-bottom: -45px;
    // padding-left: 15px
    // padding-right: 15px
    width: 100%;
    max-width: 295px;
    margin-left: auto;
    margin-right: auto;
    @include sm {
      margin-bottom: -40px; }
    @include xs {
      margin-bottom: -30px; } }
  &__text {
    margin-bottom: 10px;
    flex-grow: 1; }

  &--white {
    background-color: $white;
    box-shadow: 0px 2px 18px 0px rgba(0, 0, 0, 0.06);
    #{$this}__title {
      color: $gray-dark; } }
  &--transp {
    border-color: transparent;
    background-color: transparent;
    @include hover {
      border-color: transparent; } }
  &--transp-white {
    // background-color: rgba($white, .1)
    background-color: $black; }
  &--descr {
    padding-top: 0;
    box-shadow: 0px 0px 10px 0px rgba(2, 0, 0, 0.26);
    #{$this}__img {
      @include cover;
      padding-top: 50%;
      border-radius: 15px;
      margin-bottom: 60px;
      @include lg {
        margin-bottom: 40px; }
      @include md {
        margin-bottom: 30px; }
      @include sm {
        margin-bottom: 15px; }
      @include xs {
        margin-bottom: 10px; } }
    #{$this}__title,
    #{$this}__text {
      padding-left: 15px;
      padding-right: 15px;
      @include sm {
        padding-left: 10px;
        padding-right: 10px; } }
    #{$this}__title {
      margin-bottom: 20px;
      @include sm {
        margin-bottom: 10px; } } } }

.item-lg {
  @include flex(flex-start, normal, row, wrap);
  &__item {
    max-width: 330px;
    margin-left: auto;
    margin-right: auto; }
  &__title-lg {
    margin-bottom: 25px;
    @include semi;
    color: $white-dark;
    text-transform: uppercase;
    @include css-lock(21, 35, 320, 1499, font-size);
    @include md {
      margin-bottom: 15px; } }
  &__title {
    margin-bottom: 8px;
    @include semi;
    color: $white-dark;
    text-transform: uppercase;
    @include md {
      margin-bottom: 4px; } }
  &__text {
    color: $gray-light; }
  &__left {
    width: percentage(5/12);
    @include xs {
      width: 100%;
      margin-bottom: 15px; } }
  &__rigth {
    // +flex(flex-start, normal, row, wrap)
    width: percentage(7/12);
    padding-right: 85px;
    @include lg {
      padding-right: 0; }
    @include xs {
      width: 100%; } }
  &__block {
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } }
  &__block-auto {
    display: inline-block;
    vertical-align: top;
    @include notlast {
      margin-right: 75px; } } }

