.gallery {
  @include flex(flex-start, normal, row, wrap);
  margin-left: -15px;
  margin-right: -15px;
  @include md {
    margin-left: -10px;
    margin-right: -10px; }
  @include xs {
    margin-left: -5px;
    margin-right: -5px; }
  .gallery-slide {
    margin-left: 15px;
    margin-right: 15px;
    width: calc(50% - 30px);
    margin-bottom: 30px;
    padding-top: 25%;
    @include md {
      margin-left: 10px;
      margin-right: 10px;
      width: calc(50% - 20px);
      margin-bottom: 20px; }
    @include xs {
      margin-left: 5px;
      margin-right: 5px;
      width: calc(50% - 10px);
      margin-bottom: 10px; } } }

.gallery-slide {
  @include cover;
  border-radius: 15px;
  padding-top: 20%;
  display: block;
  @include md {
    padding-top: 30%; }
  @include sm {
    padding-top: 40%; }
  @include xs {
    padding-top: 40%; } }
