.cart-item {
  @include flex(flex-start, center);
  display: flex !important;
  position: relative;
  border-bottom: 1px solid  $gray;
  padding-top: 20px;
  padding-bottom: 20px;
  @include xs {
    flex-wrap: wrap; }
  @include before {
    display: none !important; }
  &__img {
    // max-width: 20%
    width: 20%;
    @include lg {
      width: 15%; }
    @include sm {
      width: 10%; }
    @include xs {
      width: 30%; } }
  &__title {
    // flex-grow: 1
    text-transform: uppercase;
    color: $white-dark;
    @include semi;
    padding-left: 30px;
    padding-right: 10px;
    width: 45%;
    text-align: left;
    @include lg {
      padding-left: 15px; }
    @include sm {
      width: 40%; }
    @include xs {
      width: 70%; }
    small {
      display: block;
      @include light;
      font-size: 13px;
      color: $c-text; } }
  &__price {
    width: calc(35% - 162px);
    padding-left: 70px;
    @include xl {
      // padding-left: 50px
      padding-left: 10px;
      text-align: right;
      width: calc(40% - 162px); }
    @include sm {
      width: calc(50% - 162px); }
    @include xs {
      width: 50%; } }
  &__counter {
    // width: 18%
    width: 162px;
    @include xs {
      width: 50%;
      margin-top: 15px;
      @include flex; } }

  &__close {
    @include icon-close(20, 1, currentColor);
    position: absolute;
    top: 15px;
    right: 0;
    z-index: 1;
    @include sm {
      top: 0; } } }
