.form {
  &__field {
    margin-bottom: 15px; }
  &__field-mb-md {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__checkbox {
    .checkbox {
      display: inline-block; } }
  &__btn {
    padding-top: 10px;
    @include notlast {
      margin-bottom: 15px; }
    .border {
      max-width: 290px;
      margin-left: auto;
      margin-right: auto;
      @include xs {
        max-width: none; } } }
  &__row {
    @include flex(flex-start, normal, row, wrap);
    margin-left: -15px;
    margin-right: -15px;
    @include notlast {
      margin-bottom: 65px;
      @include lg {
        margin-bottom: 40px; }
      @include md {
        margin-bottom: 30px; }
      @include sm {
        margin-bottom: 15px; } } }
  &__col {
    width: 50%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; }
    @include xs {
      width: 100%; }
    &.is-hidden {
      display: none; } }
  &__col-lg {
    @include lg {
      width: 100%; } }
  &__col-md {
    @include md {
      width: 100%; } }
  &__radios {
    @include flex(space-between, center);
    @include xs {
      flex-direction: column;
      align-items: flex-start; } } }

.form-block {
  &__title {
    color: $gray-light;
    @include bold;
    text-transform: uppercase;
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; } } }

.input {
  // color: $gray
  input {
    height: 55px;
    background-color: transparent;
    width: 100%;
    border: 1px solid $gray;
    padding: 10px 20px;
    border-radius: 30px;
    @include tr(border-color .3s);
    appearence: none;
    &:focus {
      border-color: $accent; }
    @include placeholder {
      color: $gray; }
    @include sm {
      height: 45px;
      padding: 5px 15px; }
    @include xs {
      height: 40px; } } }

.textarea {
  textarea {
    background-color: transparent;
    resize: none;
    border-radius: 15px;
    border: 1px solid $gray;
    padding: 20px;
    width: 100%;
    min-height: 130px;
    @include tr(border-color .3s);
    &:focus {
      border-color: $accent; }
    @include placeholder {
      color: $gray; }
    @include sm {
      padding: 15px; } } }
