.hero {
  background-color: $black;
  position: relative;
  .section-img {
    z-index: 1;
    opacity: 0;
    @include xl {
      img {
        max-width: 80%; } }
    @include lg {
      display: none; } }
  &__content {
    max-width: 630px; }
  &__title, &__btns {
    margin-bottom: 25px;
    opacity: 0;
    @include md {
      margin-bottom: 15px; } }
  &__text {
    margin-bottom: 50px;
    opacity: 0;
    max-width: 550px;
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 15px; } }
  &__btns {
    @include flex;
    @include md {
      display: block; } }
  &__btn {
    max-width: 295px;
    width: 100%;
    @include notlast {
      margin-right: 15px;
      @include md {
        margin-right: 0;
        margin-bottom: 10px; } } }
  &__del {
    color: $white;
    opacity: 0;
    .icon {
      font-size: 45px;
      margin-right: 10px;
      vertical-align: middle; } }
  &__breadcrumbs {
    @extend .main__breadcrumbs;
    padding-bottom: 0; }
  &__bottom-layer {
    position: absolute;
    bottom: -1px;
    left: 0;
    right: 0;
    z-index: 0;
    transform: scaleY(-1);
    font-size: 0; }


  .hero-inner {
    @include cover;
    @include lg {
      .container {
        position: relative;
        z-index: 1; }
      @include before {
        @include coverdiv;
        background-color: rgba($black, .6);
        z-index: 0; } }
    &__inner {
      @include flex(center, normal, column);
      padding-top: 10%;
      padding-bottom: 20%;
      @include rh(700) {
        padding-top: 12.5vh; }
      @include r(1600) {
        padding-left: 45px;
        padding-right: 45px; }
      @include sm {
        padding-top: 10% !important; }
      @include xs {
        padding-left: 0;
        padding-right: 0; } }

    &--noisy {
      @extend .section--noisy; }
    &--sm {
      .hero-inner__inner {
        padding-top: 5%;
        padding-bottom: 10%; } } } }
