body {
  background: $white;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  @include light;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: 14px; } }

input, textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  @include placeholder {
    font-family: inherit;
    font-size: inherit; } }

button {
  font-size: inherit;
  font-family: inherit;
  color: inherit;
  overflow: visible;
  background-color: transparent; }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.logo {
  display: inline-block;
  font-size: 0;
  @include md {
    width: 70px;
    height: 102px; }
  @include xs {
    width: 50px;
    height: 73px; } }

.out {
  padding-top: $header-height;
  @include lg {
    padding-top: $header-height-lg; }
  @include md {
    padding-top: $header-height-md; }
  @include sm {
    padding-top: $header-height-sm; } }

.lazy {
  &.is-loaded {
    animation: fadeIn 1s; } }

.main {
  display: block;
  position: relative;
  &__breadcrumbs {
    // padding-top: 25px
    padding-top: 70px;
    padding-bottom: 25px;
    @include md {
      padding-top: 70px; }
    @include sm {
      padding-top: 60px; }
    @include xs {
      padding-top: 40px; }
    @include xss {
      padding-bottom: 10px; } }
  &__img {
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
    width: percentage(530/1920);
    overflow: hidden;
    .lazy {
      &.is-loaded {
        position: relative;
        display: inline-block;
        margin-top: -10%;
        right: -20%; } } }
  &--noisy {
    @extend .section--noisy; } }

.container {
  max-width: 1440px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto; }

.price {
  @include bold;
  color: $white-dark;
  font-size: 24px;
  line-height: 1;
  small {
    font-size: 0.55em; }
  &--red {
    color: $accent; }
  &--gray-dark {
    color: $gray-dark; }
  &--sm {
    font-size: 14px; }
  &--md {
    font-size: 36px;
    @include lg {
      font-size: 30px; }
    @include md {
      font-size: 26px; } }
  &--lg {
    font-size: 40px;
    @include lg {
      font-size: 32px; }
    @include md {
      font-size: 28px; } }
  &--old {
    position: relative;
    @include after {
      width: 100%;
      top: 50%;
      transform: translate(0, -50%);
      left: 0;
      position: absolute;
      height: 1px;
      background-color: currentColor; } } }

.amount {
  // +light
  font-size: 13px;
  color: $gray-light;
  .price & {
    font-size: inherit;
    span {
      font-size: 0.55em; } }
  &--gray {
    color: $gray; } }

.counter {
  $this: &;
  color: $gray-light;
  @include flex(center, center);
  input, button {
    height: 42px;
    background-color: transparent;
    text-align: center; }
  input {
    width: 60px;
    padding: 5px 10px; }
  button {
    font-size: 1.5em;
    line-height: 42px;
    width: 50px;
    &:first-child {
      border-right: 1px solid $c-border;
      border-top-left-radius: 20px;
      border-bottom-left-radius: 20px; }
    &:last-child {
      border-left: 1px solid $c-border;
      border-top-right-radius: 20px;
      border-bottom-right-radius: 20px; } }
  &__inner {
    background-color: $black;
    border-radius: 20px;
    @include flex(center, center);
    border: 1px solid $white; }
  &--white {
    input, button {
 }      // background-color: $white
    #{$this}__inner {
      background-color: $white;
      box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.17); } } }

.border {
  @include css-lock(8, 18, 320, 1499, padding);
  position: relative;
  color: $gray;
  @include image('border-sm.png');
  @include cover;
  background-size: 100%;
  &--sm-dark {
    @include image('border-sm-dark.png'); }
  &--inl-block {
    display: inline-block; } }

.quote-fu {
  position: relative;
  @include image('border-title.png');
  @include cover;
  background-size: 100%;
  @include lightit;
  padding: 90px 40px;
  background-color: $white;
  border-radius: 120px;
  @include xl {
    padding: 70px 30px;
    border-radius: 80px; }
  @include lg {
    padding: 50px 25px;
    border-radius: 55px; }
  @include md {
    padding: 30px 20px; }
  @include sm {
    padding: 20px 15px; }
  @include xs {
    padding: 10px; }
  .icon {
    fill: $accent;
    position: absolute;
    font-size: 50px;
    bottom: -10px;
    right: 20%;
    @include lg {
      font-size: 35px;
      bottom: -15px; } } }

.quote {
  position: relative;
  background-color: $white;
  @include lightit;
  border-left: 3px solid $accent;
  padding: 30px;
  box-shadow: 0px 3px 57px 0px rgba(0, 0, 0, 0.11);
  @include lg {
    padding: 20px; }
  @include sm {
    padding: 15px; }
  p {
    @include notlast {
      margin-bottom: 30px;
      @include md {
        margin-bottom: 15px; } } } }

.reccomend {
  padding-top: 85px;
  @include xl {
    padding-top: 70px; }
  @include lg {
    padding-top: 60px; }
  @include md {
    padding-top: 50px; }
  @include sm {
    padding-top: 30px; }
  @include xs {
    padding-top: 20px; }
  &__title {
    margin-bottom: 50px; } }

.date {
  font-size: 13px;
  @include inline-flex(flex-start, center);
  .icon {
    margin-right: 5px;
    font-size: 18px; } }

.added_to_cart {
  display: none !important; }
