.breadcrumbs {
  @include light;
  font-size: 13px;
  color: $white-dark;
  margin-bottom: 25px;
  @include md {
    margin-bottom: 15px; }
  @include sm {
    margin-bottom: 10px; }
  li {
    display: inline-block; }
  a {
    @include tr(color .3s);
    @include hover {
      color: $gray; } }

  .is-active {
    pointer-events: none;
    color: $gray; } }

.back-link {
  position: relative;
  display: inline-block;
  padding-left: 30px;
  @include tr(color .3s);
  @include hover {
    color: $white-dark; }
  &::before {
    @include icon-arrow(8, 8, 1, currentColor, 45);
    transform: translate(0, -50%) rotate(45deg); }
  &::after {
    width: 20px;
    height: 1px;
    background-color: currentColor;
    transform: translate(0, -50%); }
  &::before, &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0; } }

