.features {
  // +clr
  @include flex(flex-start, normal, row, wrap);
  &--simple {
    .feature {
      padding-left: 120px;
      @include md {
        padding-left: 90px; }
      @include sm {
        padding-left: 70px; }
      @include xss {
        padding-left: 0; }

      &:nth-child(odd) {
        width: 50%;
        padding-left: 120px;
        @include md {
          padding-left: 90px; }
        @include sm {
          padding-left: 70px;
          width: 100%; }
        @include xss {
          padding-left: 0; }
        .feature__img {
          @include xs {
            position: absolute; }
          @include xss {
            position: static; } } }
      &:nth-child(even) {
        width: 50%;
        @include sm {
          width: 100%; } }
      &:nth-child(3) {
        .feature__img {
          img[data-loaded="true"] {
            left: auto; } } }
      &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100px;
        @include xll {
          width: 100px; }
        @include xl {
          width: 100px; }
        @include lg {
          width: 100px; }
        @include md {
          width: 80px; }
        @include sm {
          width: 70px; }
        @include xss {
          position: static; } }
      &__title {
        margin-bottom: 20px; } } } }

.feature {
  $this: &;
  width: 50%;
  position: relative;
  margin-bottom: 100px;
  @include xl {
    margin-bottom: 80px; }
  @include lg {
    margin-bottom: 60px; }
  @include md {
    margin-bottom: 40px; }
  @include sm {
    margin-bottom: 25px; }
  &:nth-child(odd) {
    padding-left: 270px;
    width: percentage(7/12);
    @include xl {
      padding-left: 240px; }
    @include lg {
      padding-left: 180px; }
    @include md {
      padding-left: 150px; }
    @include sm {
      padding-left: 125px; }
    @include xs {
      padding-left: 0;
      width: 50%; }

    #{$this}__img {
      position: absolute;
      top: 0;
      left: 0;
      @include xs {
        position: static; } } }
  &:nth-child(even) {
    width: percentage(5/12);
    @include xs {
      width: 50%; } }
  &:nth-child(3) {
    #{$this}__img {
      img[data-loaded="true"] {
        left: -30%;
        position: relative;
        @include xll {
          left: 0; } } } }

  &__title {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__img {
    // text-align: center
    @include xll {
      width: 260px; }
    @include xl {
      width: 220px; }
    @include lg {
      width: 180px; }
    @include md {
      width: 150px; }
    @include sm {
      width: 125px; } }
  &__text {
    p {
      @include notlast {
        margin-bottom: 20px;
        @include md {
          margin-bottom: 10px; } } } }
  &__img-content {
    margin-top: 100px;
    // margin-bottom: 100px
    text-align: center;
    @include xl {
      margin-top: 80px; }
    @include lg {
      margin-top: 60px; }
    @include md {
      margin-top: 40px; }
    @include sm {
      margin-top: 25px; } }
  &__content {
    padding-left: 10px;
    padding-right: 10px;
    @include xs {
      padding-left: 5px;
      padding-right: 5px; } } }
