.banner {
  background-color: rgba($black, .3);
  border-radius: 15px;
  padding: 40px 30px;
  @include lg {
    padding: 30px; }
  @include md {
    padding: 20px; }
  @include sm {
    padding: 15px; }
  &__title {
    text-transform: uppercase;
    @include semi;
    color: $gray; }
  &__items {
    padding-top: 40px;
    border-bottom: 1px solid $gray;
    margin-bottom: 30px;
    @include lg {
      padding-top: 30px; }
    @include md {
      padding-top: 15px; } }
  &__amount {
    @include flex(space-between, center);
    margin-bottom: 30px; }
  &__btn {
    max-width: 285px;
    margin-left: auto;
    margin-right: auto;
    @include notlast {
      margin-bottom: 30px; } }
  &__checkbox {
    @include flex(center); } }

.banner-item {
  margin-bottom: 30px;
  @include md {
    margin-bottom: 15px; }
  &__inner {
    @include flex(space-between, center); }
  &__amount {
    text-align: right; }
  &__title {
    text-transform: uppercase;
    @include semi;
    color: $white-dark;
    padding-right: 10px; } }
