.social {
  color: $gray-light;
  li {
    display: inline-block;
    vertical-align: middle;
    @include notlast {
      margin-right: 30px; } }
  a {
    display: block;
    @include tr(color .3s);
    @include hover {
      color: $gray; } }
  .icon {
    font-size: 20px; }
  .icon-google-plus {
    font-size: 1.5em; }

  &--vert {
    color: $white-dark;
    li {
      display: block;
      @include notlast {
        margin-right: 0;
        margin-bottom: 15px; } }
    a {
      @include hover {
        color: $gray-light; } } } }
