.header {
  $this: &;
  // position: relative
  z-index: 10;
  @include tr(transform .3s);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  &.is-hidden {
    display: none; }
  &__inner {
    position: relative;
    @include flex(flex-start, center);
    // padding-left: 240px
    @include css-lock(120, 240, 992, 1499, padding-left);
    @include sm {
      padding-left: 80px; }
    @include xs {
      padding-left: 40px; } }
  &__top {
    background-color: $white;
    height: $header-top-height;
    @include lg {
      height: $header-top-height-lg; }
    @include md {
      height: $header-top-height-md; }
    @include sm {
      height: $header-top-height-sm; }
    @include r(374) {
      #{$this}__inner {
        padding-top: 10px; } } }

  &__bottom {
    background-color: $black-light;
    box-shadow: 0px 6px 7px 0px rgba(0, 0, 0, 0.45);
    height: $header-bottom-height;
    @include lg {
      height: $header-bottom-height-lg; }
    @include md {
      position: fixed;
      overflow: auto;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 10;
      transform: translate(-102%, 0);
      @include vertical;
      @include ready {
        @include tr(transform .3s); }
      .container {
        display: inline-block;
        vertical-align: middle;
        font-size: 16px;
        width: 100%;
        text-align: left;
        @include sm {
          font-size: 14px; } }
      &.is-active {
        transform: translate(0, 0); } }
    @include xss {
      @include vertical;
      .container {
        display: inline-block;
        vertical-align: middle;
        font-size: 14px;
        width: 100%; } }
    #{$this}__inner {
      @include md {
        padding-left: 0;
        justify-content: space-between; }
      @include xss {
        display: block;
        padding-top: 15px;
        padding-bottom: 15px;
        text-align: center;
        .nav {
          margin-bottom: 15px; } } } }
  &__contact {
    position: relative;
    padding-left: 50px;
    padding-right: 50px;
    @include after {
      height: 30px;
      border-right: 1px solid $c-border;
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      right: 0; }
    @include lg {
      padding-left: 25px;
      padding-right: 25px; }
    @include sm {
      padding-left: 10px;
      padding-right: 10px; }
    @include xss {
      padding-left: 5px;
      padding-right: 5px; } }
  &__top-right-mob {
    display: none;
    @include md {
      display: block; }
    @include xss {
      @include flex(center, center); } }
  &__top-right {
    @include flex(space-around, center);
    flex-grow: 1;
    @include md {
      display: none; } }
  &__logo {
    position: absolute;
    // top: -20px
    top: 0;
    left: 0;
    z-index: 1;
    // +lg
    //   top: -10px
    // +sm
    //   top: -5px
    @include xs {
      left: -15px; } }
  &__cart {
    position: relative;
    width: 175px;
    &:hover {
      .tooltip {
        visibility: visible;
        opacity: 1; } }
    @include md {
      flex-grow: 1;
      padding-left: 15px;
      @include flex(flex-end); }
    @include xss {
      padding-left: 5px; } }
  &__meta {
    @include md {
      margin-bottom: 15px; }
    @include xss {
      margin-bottom: 0;
      margin-right: 10px;
      margin-left: 10px; } }
  &__burger {
    display: none;
    margin-left: 10px;
    @include md {
      display: block; }
    @include xss {
      margin-left: 5px; } }
  &__marquee {
    @include sm {
      display: none; } }
  &__close {
    top: 15px;
    right: 15px;
    z-index: 1;
    color: $white;
    background-color: transparent;
    @include icon-close(25, 2, currentColor);
    position: absolute;
    @include tr(color .3s);
    display: none;
    @include hover {
      color: currentColor; }
    @include md {
      display: block; } } }

.header-contact {
  @include xss {
    font-size: 13px; }
  &__title {
    color: $gray;
    font-size: 13px;
    margin-bottom: 5px;
    @include xs {
      font-size: 11px; }
    @include r(374) {
      display: none; } }
  &__phone {
    color: $black;
    @include bold;
    @include r(374) {
      font-size: 11px; } }
  &__social-center {
    text-align: center; }
  &--first {
    padding-left: 0 !important; } }

.header-cart {
  padding-left: 30px;
  position: relative;
  color: $gray;
  display: block;
  @include sm {
    padding-left: 0; }
  &__btn {
    text-decoration: underline;
    color: $accent;
    background-color: transparent;
    display: block;
    @include sm {
      display: none; } }
  &__title {
    font-size: 13px;
    display: block;
    @include text-overflow; }
  &__content {
    display: block;
    @include sm {
      display: none; } }
  &__icon {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    text-decoration: none;
    display: block;
    white-space: nowrap;
    @include sm {
      color: $accent;
      position: relative; }
    .icon {
      font-size: 21px;
      @include xss {
        font-size: 16px; } }
    sup {
      font-size: 0.7em;
      vertical-align: top;
      position: relative;
      top: -.5em;
      right: .5em;
      display: none;
      @include sm {
        display: inline-block; } } } }

.marquee {
  color: $white;
  background-color: #2c2d2d;
  padding-top: 15px;
  padding-bottom: 15px;
  white-space: nowrap;
  overflow: hidden;
  &__text {
    animation: marquee 25s linear infinite; } }

.burger {
  display: block;
  width: 28px;
  height: 28px;
  margin-top: -14px;
  background-color: transparent;
  @include xss {
    width: 20px;
    height: 20px; }
  span:after, span:before {
    content: "";
    position: absolute;
    left: 0;
    top: 9px;
    @include xs {
      top: 5px; } }
  span:after {
    top: 18px;
    @include xs {
      top: 10px; } }
  span {
    position: relative;
    display: block; }
  span, span:after, span:before {
    width: 100%;
    height: 2px;
    background-color: currentColor;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; }
  // &.is-active span
  //   background-color: transparent
  // &.is-active span:before
  //   transform: rotate(45deg) translate(-1px, 0px)
  // &.is-active span:after
  //   transform: rotate(-45deg) translate(6px, -7px)

  @keyframes marquee {
    0% {
      transform: translate(100%, 0); }
    100% {
      transform: translate(-105%, 0); } } }


