.footer {
  @include cover;
  color: $gray-light;
  &__inner {
    @include flex(flex-start, normal, row, wrap);
    margin-left: -15px;
    margin-right: -15px;
    position: relative;
    padding-bottom: 55px; }
  &__col {
    padding-left: 15px;
    padding-right: 15px;
    width: 25%;
    @include notfirst {
      padding-top: 70px;
      @include md {
        padding-top: 50px; }
      @include xss {
        padding-top: 15px; } }
    @include sm {
      width: 50%; }
    @include xss {
      width: 100%; } }
  &__col-flex {
    @include flex(space-between);
    padding-right: 85px;
    @include md {
      padding-right: 15px; } }
  &__auth {
    position: absolute;
    font-size: 12px;
    bottom: 55px;
    right: 15px;
    z-index: 1;
    @include sm {
      position: static;
      text-align: center;
      padding-top: 15px;
      width: 100%;
      padding-left: 15px;
      padding-right: 15px; }
    a {
      text-decoration: underline; } }

  // &__social
  //   position: absolute
  //   right: 70px
  //   top: 80px
  //   +md
  //     right: 15px
  &__title {
    text-transform: uppercase;
    color: $white-dark;
    @include bold;
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; } }
  &__logo {
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; } }
  &__copy {
    font-size: 12px;
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; }
    p {
      @include notlast {
        margin-bottom: 15px; } } }
  &__pay {
    font-size: 12px;
    @include flex(flex-start, center);
    span {
      display: block;
      margin-right: 20px;
      @include sm {
        margin-right: 0; } }
    img {
      display: block;
      @include notlast {
 } } } }        // margin-right: 10px

