.popup {
  $this: &;
  position: fixed;
  z-index: 20;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba($black, .7);
  @include vertical;
  overflow: auto;
  padding: 15px;
  visibility: hidden;
  opacity: 0;
  @include ready {
    @include tr(opacity .3s, visibility .3s); }
  &.is-active {
    visibility: visible;
    opacity: 1; }
  &__inner {
    background-color: $white;
    display: inline-block;
    vertical-align: middle;
    font-size: $fz;
    border-radius: 15px;
    width: 100%;
    max-width: 1415px;
    padding: 75px 125px 95px;
    position: relative;
    @include xll {
      padding: 50px 100px 75px; }
    @include xl {
      padding: 50px 90px; }
    @include lg {
      padding: 40px 70px; }
    @include md {
      padding: 40px; }
    @include sm {
      padding: 20px; }
    @include xs {
      padding: 15px; }
    @include sm {
      font-size: $fz-sm; } }
  &__title {
    margin-bottom: 70px;
    @include xl {
      margin-bottom: 60px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 15px; } }
  &__title-sm {
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } }
  &__title-xs {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__subttl {
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } }
  &__subttl-sm {
    max-width: 320px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__btns {
    margin-bottom: 30px;
    @include flex(space-between, center);
    max-width: 620px;
    margin-left: auto;
    margin-right: auto;
    @include md {
      margin-bottom: 15px; }
    @include xs {
      display: block; } }
  &__btn {
    max-width: 290px;
    width: 100%;
    @include xs {
      margin-left: auto;
      margin-right: auto;
      max-width: none;
      @include notlast {
 } } }        // margin-bottom: 10px
  &__close {
    @include icon-close(20, 1, currentColor);
    position: absolute;
    top: 30px;
    right: 30px;
    z-index: 1;
    @include md {
      top: 20px;
      right: 20px; }
    @include xs {
      top: 10px;
      right: 10px; } }
  &__layer {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    right: 0;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    overflow: hidden;
    img {
      width: 100%; } }
  &__social {
    p {
      margin-bottom: 5px; } }

  &--md {
    #{$this}__inner {
      max-width: 930px; }
    #{$this}__subttl {
      max-width: 465px;
      margin-left: auto;
      margin-right: auto; } }
  &--layer {
    #{$this}__inner {
      padding-top: 110px;
      @include xl {
        padding-top: 100px; }
      @include sm {
        padding-top: 60px; }
      @include xs {
        padding-top: 40px; } } }
  &--noisy-white {
    #{$this}__inner {
      @extend .section--noisy-white; } }
  &--sm {
    #{$this}__inner {
      max-width: 450px;
      padding: 45px 20px;
      @include md {
        padding: 35px 20px 20px; }
      @include sm {
        padding: 20px; }
      @include xs {
        padding: 15px; } } } }
