strong {
  font-weight: 400;
  @include bold; }

small {
  font-size: inherit; }

em, i {
  font-style: normal;
  @include italic; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.title {
  @include css-lock(32, 60, 320, 1499, font-size);
  @include georgia;
  line-height: 1;
  color: $gray-dark;
  em {
    font-family: inherit;
    font-style: italic; }
  &--white {
    color: $white; }
  &--h3 {
    // font-size: 35px
    @include css-lock(21, 35, 320, 1499, font-size); }
  &--h4 {
    @include css-lock(18, 24, 320, 1499, font-size); }
  &--h5 {
    @include css-lock(14, 16, 767, 1499, font-size);
    @include sm {
      font-size: $fz-sm !important; } }
  &--semi {
    @include semi; }
  &--ttn {
    text-transform: none; }
  &--ttu {
    text-transform: uppercase; }
  &--reg {
    @include reg; } }

.h1, h1 {}

.h2, h2 {}

.h3, h3 {}

.h4, h4 {}

.h5, h5 {}

.h6, h6 {}
