.events {}

.event {
  position: relative;
  @include flex;
  margin-bottom: 60px;
  @include lg {
    margin-bottom: 40px; }
  @include md {
    margin-bottom: 30px; }
  @include sm {
    margin-bottom: 15px;
    flex-wrap: wrap; }
  &__img {
    border-radius: 15px;
    @include cover;
    padding-top: 37%;
    flex: 0 0 50%;
    position: relative;
    z-index: 0;
    left: -60px;
    @include md {
      left: calc(0px - 20% - 60px); }
    @include sm {
      left: auto;
      flex: 0 0 100%;
      order: 0;
      padding-top: 50%; }
    @include xss {
      padding-top: 70%; } }
  &__content {
    padding-top: 60px;
    padding-bottom: 60px;
    flex: 0 0 calc(50% + 60px);
    position: relative;
    z-index: 2;
    @include md {
      flex: 0 0 calc(70% + 60px); }
    @include sm {
      flex: 0 0 100%;
      padding-top: 0;
      padding-bottom: 0;
      padding-left: 20px;
      padding-right: 20px;
      order: 1;
      margin-top: -70px; }
    @include xss {
      padding-left: 10px;
      padding-right: 10px; } }
  &__content-in {
    background-color: $white;
    border-radius: 15px;
    box-shadow: 0px 3px 57px 0px rgba(0, 0, 0, 0.11);
    padding: 20px 30px 20px 50px;
    height: 100%;
    @include flex(center, normal, column);
    @include xl {
      padding: 20px 30px 20px 40px; }
    @include lg {
      padding-left: 30px;
      padding-right: 30px; }
    @include md {
      padding-left: 20px;
      padding-right: 20px; }
    @include sm {
      padding-left: 15px;
      padding-right: 15px; } }
  &__title {
    margin-bottom: 25px;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  &__text {
    margin-bottom: 20px;
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  &__bottom {
    @include flex(space-between, cnenter);
    @include xss {
      flex-wrap: wrap; } }
  &__btn {
    max-width: 285px;
    width: 100%;
    @include xss {
      order: 1; } }
  &__date {
    @include flex(flex-start, center);
    @include xss {
      order: 0;
      margin-bottom: 20px; } } }
