.contacts-tabs {
  &__nav {
    @include flex(space-around, center, row, wrap);
    margin-bottom: 35px; }
  &__btn {
    display: block;
    margin-left: 10px;
    margin-right: 10px;
    background-color: transparent;
    color: $gray-light;
    @include light;
    @include tr(color .3s, transform .3s);
    &.is-active {
      color: $black;
      @include scale(1.4);
      @include lg {
        @include scale(1);
        font-size: 1.2em; } }
    @include hover {
      @extend .contacts-tabs__btn.is-active; } }
  &__item {
    max-height: 0;
    overflow: hidden;
    .contacts__content {
      display: none; }
    .contacts__block {
      .contact, .contacts__block-btn {
        @include tr(opacity .5s);
        opacity: 0; } }
    &.is-active {
      max-height: none;
      overflow: visible;
      .contacts {
        &__content {
          display: block;
          opacity: 1; }
        &__block {
          .contact, .contacts__block-btn {
            opacity: 1; } } } } } }

.contacts {
  $this: &;
  position: relative;
  @include flex(flex-end);
  @include sm {
    margin-bottom: 40px; }
  &__content {
    @include cover;
    padding-top: 40%;
    width: percentage(10/12);
    position: relative;
    margin-right: 50px;
    border-radius: 15px;
    @include md {
      padding-top: 50%;
      margin-right: 0;
      width: 100%; }
    @include xs {
      padding-top: 80%; }
    @include xss {
      padding-top: 100%; } }
  &__block-in {
    opacity: 0;
    visibility: hidden;
    max-height: 0;
    @include tr(opacity .3s);
    &.is-active {
      max-height: none;
      visibility: visible;
      opacity: 1; } }
  &__block {
    background-color: $white;
    border-radius: 15px;
    box-shadow: 0px 0px 35px 0px rgba(0, 0, 0, 0.16);
    text-align: center;
    // padding: 50px 15px 45px
    padding: 15px;
    @include flex(center, center, column);
    position: absolute;
    width: percentage(3/12);
    top: 30px;
    bottom: 30px;
    left: 0;
    z-index: 2;
    @include lg {
      width: percentage(4/12); }
    @include md {
      width: 50%; }
    @include sm {
      width: 100%;
      max-width: 400px;
      transform: translate(-50%, 0);
      left: 50%;
      top: auto;
      bottom: -40px; } }
  &__pagination {
    text-align: center;
    margin-top: 20px; }
  &__dot {
    display: inline-block;
    margin-left: 4px;
    margin-right: 4px;
    border-radius: 50%;
    background-color: $gray;
    width: 14px;
    height: 14px;
    &.is-active {
      background-color: $accent; } }

  &--map {
    @include xs {
      flex-wrap: wrap; }
    #{$this}__block {
      width: 40%;
      position: relative;
      padding-top: 35px;
      padding-bottom: 45px;
      @include lg {
        padding-top: 35px;
        padding-bottom: 35px; }
      @include md {
        padding-top: 25px;
        padding-bottom: 25px; }
      @include sm {
        padding-top: 15px;
        padding-bottom: 15px;
        transform: translate(0, 0);
        left: auto;
        bottom: auto;
        max-width: none;
        width: 50%; }
      @include xs {
        width: 100%;
        @include xs {
          order: 1; } } }
    #{$this}__content {
      margin-right: 0;
      padding-top: 0;
      width: 60%;
      @include sm {
        width: 50%; }
      @include xs {
        width: 100%;
        order: 0;
        padding-top: 100%; }
      .contact-btn {
        position: absolute;
        &[data-target-tab="hidropark"] {
          top: 20%;
          left: 20%;
          @include r(680) {
            left: 15%; }
          @include xs {
            top: 25%;
            left: 40%; }
          @include xss {
            left: 30%; } }
        &[data-target-tab="stroiteley"] {
          top: 30%;
          left: 40%;
          @include xs {
            top: 40%;
            left: 55%; }
          @include xss {
            left: 50%; } }
        &[data-target-tab="poznyaki"] {
          top: 60%;
          left: 60%;
          @include xs {
            left: 70%; } } } } } }

.contact {
  margin-bottom: 30px;
  @include md {
    margin-bottom: 15px; }
  &__title {
    @include semi;
    color: $gray-dark; } }

.contact-btn {
  $this: &;
  color: $gray-dark;
  @include semi;
  &.is-active {
    #{$this}__img {
      filter: grayscale(0);
      opacity: 1; }
    #{$this}__check {
      opacity: 1;
      @include before {
        display: block; } } }
  &__title {
    display: block;
    margin-bottom: 10px; }
  &__img {
    display: block;
    margin-bottom: 5px;
    font-size: 0;
    filter: grayscale(100%);
    opacity: .3;
    margin-left: auto;
    margin-right: auto;
    @include sm {
      width: 40px; }
    @include xss {
      width: 30px; } }

  &__check {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    width: 20px;
    height: 20px;
    border: 1px solid  $accent;
    border-radius: 50%;
    @include flex(center, center);
    opacity: .3;
    @include before {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: $accent;
      display: none; } } }

