.pay-methods {
  @include flex(flex-start, normal, row, wrap);
  margin-left: -10px;
  margin-right: -10px; }

.pay-method {
  max-width: 410px;
  &__wrap {
    width: 50%;
    @include flex(center, normal);
    padding-left: 10px;
    padding-right: 10px; }
  &__img, &__title {
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; } }
  &__img {
    width: 150px;
    height: 150px;
    @include vertical;
    @include xs {
      width: 100px;
      height: 100px; }
    img {
      display: inline-block;
      vertical-align: bottom; } } }
