.tooltip {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  padding-top: 20px;
  z-index: 10;
  top: 100%;
  right: 0;
  width: 570px;
  @include ready {
    @include tr(opacity .3s, visibility .3s); }
  @include sm {
    width: 400px; }
  @include xss {
    position: fixed;
    left: 15px;
    right: 15px;
    top: 40px;
    width: auto; }
  &__inner {
    position: relative;
    background-color: $white;
    box-shadow: 0px 0px 34px 0px rgba(0, 0, 0, 0.23);
    border-radius: 15px;
    padding: 20px;
    @include before {
      @include arr(20, 20, $white, t);
      position: absolute;
      bottom: 100%;
      right: 154px;
      @include sm {
        right: 15px; }
      @include xss {
        right: 38px; } }
    @include sm {
      padding: 10px; } } }

.tooltip-cart {
  &__items {
    position: relative;
    max-height: 465px;
    overflow: auto;
    padding-right: 15px;
    @include sm {
      max-height: 265px; } }
  &__bottom {
    @include flex(space-between, center);
    padding-top: 15px;
    @include sm {
      flex-direction: column; } }
  &__btn {
    width: 285px;
    @include sm {
      order: 1; } }
  &__amount {
    @include sm {
      order: 0; } } }

.tooltip-cart-item {
  position: relative;
  @include flex(flex-start, center);
  padding-right: 30px;
  padding-top: 30px;
  padding-bottom: 40px;
  border-bottom: 1px solid  $gray-light;
  @include md {
    padding-right: 20px;
    padding-bottom: 20px; }
  @include sm {
    flex-wrap: wrap;
    padding-right: 0; }
  &__img {
    max-width: 25%;
    font-size: 0;
    @include sm {
      max-width: 100px;
      margin-left: auto;
      margin-right: auto;
      padding-top: 10px;
      padding-bottom: 10px; }
    img {
      min-width: 10px;
      min-height: 10px; }
    @include sm {
      // width: 70px
 } }      // flex: 0 0 70px
  &__middle {
    padding-left: 20px;
    padding-right: 20px;
    flex-grow: 1;
    @include sm {
      padding-left: 0;
      padding-right: 0;
      width: 100%;
      text-align: center; } }
  &__price {
    border-left: 1px solid  $gray-light;
    border-right: 1px solid  $gray-light;
    padding-left: 15px;
    padding-right: 15px;
    @include sm {
      width: 100%;
      margin-top: 15px;
      border-right: 0;
      border-left: 0;
      padding-left: 0;
      padding-right: 0;
      text-align: center; } }
  &__title {
    text-transform: uppercase;
    font-size: 13px;
    margin-bottom: 5px;
    @include semi;
    color: $gray-dark;
    small {
      display: block;
      color: $gray;
      @include light; } }
  &__counter {
    @include flex;
    @include sm {
      display: block; } }
  &__close {
    @include icon-close(16, 1, currentColor);
    position: absolute;
    z-index: 1;
    top: 10px;
    right: 0; } }
